  padding: 20px;
  border-top: 1px solid rgba(160, 160, 160, 0.2);
}

.card .card-action i {
  font-size: 18px;
  vertical-align: middle;
}

.card .card-action a {
  font-size: 15px;
  color: #ffab40;
  text-transform: uppercase;
  margin-right: 20px;
  transition: color 0.3s ease;
